import React from "react";

const Modal2 = () => {


    return (

        <div className="container-fluid">
            <div className="modal fade" id="Modal2" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered modal-lg modal-xl" role="document">
                    <div className="modal-content">
                        <div className="modal-header text-center">
                            <div className="text-center w-100">
                                <h5 className="modal-title modaltitlesize" id="exampleModalLongTitle">Neural Style Transfer</h5>
                            </div>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="text-center p-3">
                            <img src={require("./modalimgs/styletransfer.PNG")} className="img-fluid rounded" alt="Responsive image"></img>
                        </div>
                        <div className="modal-body promodaltextsize">
                            <div className="p-4">
                                <h3 className="text-center">Deep Learning - Style Transfer</h3>
                                <h4 className="text-center m-2">Language: Python</h4>
                                <h4 className="text-center m-2">Framework: Tensorflow</h4>
                                <h4 className="text-center m-2">VGG-19 | Magenta
                                <br></br><br></br></h4>
                                <h4>Background<br></br><br></br></h4>
                                <p>
                                    Neural style transfer utilizes the heavily-used deep learning practice "transfer learning". Transfer learning focuses on storing knowledge gained while solving one problem and applying it to a different but related problem, often saving countless hours and funds.  
                                </p>
                                <p>
                                    This projects explores two neural style transfer methods:
                                </p>
                                <ul>
                                    <li>
                                        Neural Style Transfer: utilizes VGG-19, a convolutional neural network that is 19 layers deep. You can load a pretrained version of the network trained on more than a million images from the ImageNet database as well as dissect the network and extract important portions for further development. 
                                    </li>
                                    <li>
                                        Fast Neural Style Transfer: utilizes Majenta, an open source Python library, powered by TensorFlow. This library includes utilities for manipulating source data (primarily music and images), using this data to train machine learning models, and generating new content from these models.
                                    </li>
                                </ul>
                                <h4>Model Implementation<br></br><br></br></h4>
                                <p>
                                    The goal of this project was to gain a better understanding of what transfer learning is, and how it can be implemented into a model to save time for further experimentation. Given my interest in art design, the project itself seemed like an extremely fascinating way to challenge myself, while giving others an entertaining tool. 
                                </p>
                                <p>
                                    This project was completed in two parts, both very much inspired and modeled off of pre-existing projects displayed on the Tensorflow.org. 
                                    <br></br><br></br> 
                                </p>
                                <h4>Reflection <br></br><br></br></h4>
                                <p>
                                    My inspiration was first sparked during my Deep Learning Specialization offered by Deeplearning.ai on Coursera. A similar project was provided to the students, where the VGG-19 network was used but a portion of the network was manipulated while the remainder was left untouched. The objective was to manipulate a small portion to better fit the objective of the user, while using the extremely complex and well-trained pre-existing network as a foundation. 
                                </p>
                                <p>
                                    Similarly, I applied this concept to the "Neural Style Transfer" network, comparing the differences between using the trained network with the top three fully connected layers and the trained network without the top three layers. This allowed me to better understand what was taking place in the intermediate layers of the pre-trained image classification network to define the style and content representations before proceeding within the neural network. 
                                </p>
                                <p>
                                    The "Fast Neural Style Transfer" allowed me to understand how to directly utilize pre-trained models for related deep learning applications. Magenta is an open source research project exploring the role of machine learning as a tool in the creative process with tremendous potential, being recognized for creating art and music. 
                                </p>
                                <p>
                                    I feel this project allowed me to better understand how to apply the information stored in complex pre-trained models to creative new ideas. I also feel I have learned a lot about the frameworks of convolutional neural networks, and how they can be dissected and manipulated to better structure them for a given purpose. I am interested to learn more about how transfer learning can be used to process complex problems using models that carry different and even unconventional perspectives.     
                                </p>
                            </div>
                        </div>
                        <div className="modal-footer">
                            <button type="button"
                            className="btn btn-outline-dark p-2 m-2 col-md-2" 
                            data-dismiss="modal">
                                Close
                            </button>
                                <a href="https://github.com/scottvcaputo/Neural-Style-Transfer"
                                target="_blank"
                                rel="noopener noreferrer"
                                type="button" 
                                className="btn btn-outline-secondary p-2 m-2 col-md-2">
                                    <i className="fab fa-github" /> Source
                                </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Modal2;
